<template>
  <div class="box" v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-button slot="append" icon="el-icon-refresh" @click="refresh"></el-button>
      </div>

      <div class="btn">
        <span>学校：</span>
        <el-select v-model="school" placeholder="请选择学校" @change="changeSchoolType">
          <el-option v-for="item in schoolList" :key="item.index" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
      <div class="btn">
        <span>年级：</span>
        <el-select v-model="grade" placeholder="请选择年级" @change="changeGradeType">
          <el-option v-for="item in gradeList" :key="item.index" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
    </div>
    <div id="bar-chart"></div>
    <div id="bar-chart1"></div>
  </div>
</template>

<script>
  import API from "@/api/index.js";

  require("echarts");
  export default {
    inject: ["reload"],
    data() {
      return {
        loading: true,
        school: "",
        grade: "",
        schoolList: [],
        gradeList: [],
        school_id: "",
        grade_id: "",
        bar: {
          title: {
            text: "收费统计",
            left: "center",
          },
          tooltip: {},
          legend: {
            orient: "vertical",
            data: ["学生总数", "缴费总数"],
            left: "right",
          },
          xAxis: {
            data: [],
          },
          yAxis: {},
          series: [],
          dataZoom: [{
            type: "inside",
          }, ],
        },
        bar1: {
          title: {
            text: "收费统计",
            left: "center",
          },
          tooltip: {},
          legend: {
            orient: "vertical",
            data: ["缴费一年人数", "缴费两年人数"],
            left: "right",
          },
          xAxis: {
            data: [],
          },
          yAxis: {},
          series: [],
          dataZoom: [{
            type: "inside",
          }, ],
        },
        nameList: [],
        oneYear: [],
        twoYear: [],
        studentList: [],
        payList: [],
      };
    },
    mounted() {
      this.getSchool();
      this.getTotal();
    },
    methods: {
      // 刷新
      refresh() {
        this.reload();
      },
      getSchool() {
        var self = this;
        API.schools(1, 1000).then((res) => {
          self.schoolList = res.data;
        });
      },

      fucTotal(school_id, grade_id) {
        var self = this;
        let barChart = self.$echarts.init(document.getElementById('bar-chart'));
        let barChart1 = self.$echarts.init(document.getElementById('bar-chart1'));
        barChart.setOption(self.bar);
        barChart1.setOption(self.bar1);
        API.payTotal(school_id, grade_id).then((res) => {
          res.forEach((item) => {
            self.nameList.push(item.name);
            self.oneYear.push(item.count_48);
            self.twoYear.push(item.count_92);
            self.studentList.push(item.student_count);
            self.payList.push(item.count);
          });
          var zoomSize = 6;
          barChart.on("click", function (params) {
            console.log(
              self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)]
            );
            barChart.dispatchAction({
              type: "dataZoom",
              startValue: self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)],
              endValue: self.nameList[
                Math.min(
                  params.dataIndex + zoomSize / 2,
                  self.nameList.length - 1
                )
              ],
            });
          });
          barChart1.on("click", function (params) {
            console.log(
              self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)]
            );
            barChart1.dispatchAction({
              type: "dataZoom",
              startValue: self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)],
              endValue: self.nameList[
                Math.min(
                  params.dataIndex + zoomSize / 2,
                  self.nameList.length - 1
                )
              ],
            });
          });
          barChart.setOption(
            (self.bar = {
              xAxis: {
                data: self.nameList,
              },
              series: [{
                  name: "学生总数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.studentList,
                },
                {
                  name: "缴费总数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.payList,
                },
              ],
            })
          );
          barChart1.setOption(
            (self.bar1 = {
              xAxis: {
                data: self.nameList,
              },
              series: [{
                  name: "缴费一年人数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.oneYear,
                },
                {
                  name: "缴费两年人数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.twoYear,
                },
              ],
            })
          );
          self.loading = false;
        });
      },

      getTotal() {
        var self = this;
        self.fucTotal();
      },

      changeSchoolType(value) {
        var self = this;
        self.loading = true;
        self.school_id = value;
        self.grade = '';
        self.nameList = [];
        self.oneYear = [];
        self.twoYear = [];
        self.studentList = [];
        self.payList = [];
        API.statistics(self.school_id).then((res) => {
          self.gradeList = res.data;
        });
        self.fucTotal(self.school_id);
      },

      changeGradeType(value) {
        var self = this;
        self.loading = true;
        self.grade_id = value;
        self.nameList = [];
        self.oneYear = [];
        self.twoYear = [];
        self.studentList = [];
        self.payList = [];
        self.fucTotal(self.school_id, self.grade_id);
      },
    },
  };
</script>

<style lang="scss" scoped>
  .box {
    overflow: hidden;
  }

  #bar-chart {
    width: 100%;
    height: 500px;
  }

  #bar-chart1 {
    width: 100%;
    height: 500px;
  }
</style>